@import './styles/typography.scss';

body {
  -webkit-font-smoothing: antialiased;
  .reveal .slides {
    // font-family: 'Gothic A1';
    font-family: 'Montserrat ExtraBold', 'Black Han Sans';
    // font-family: 'Noto Sans KR';
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-family: 'Montserrat ExtraBold', 'Black Han Sans';
      letter-spacing: initial;
      text-transform: none;
    }
    pre {
      code {
        padding: 12px 16px;
      }
    }
    ul,
    ol {
      margin-left: 20px;
      padding-left: 20px;
    }
    ul ul,
    ul ol,
    ol ol,
    ol ul {
      margin-left: 20px;
      padding-left: 20px;
    }
  }
  .reveal .slides {
    em {
      font-style: normal;
      font-weight: 600;
      color: #00a542;
    }
    .primary {
      color: #00a542;
    }
    .gray {
      color: #aaa;
    }
